rrect display in IE 9-.\n * 2. Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n    display: inline-block; /* 1 */\n    vertical-align: baseline; /* 2 */\n}\n\n/**\n * Remove the default vertical scrollbar in IE.\n */\n\ntextarea {\n    overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10-.\n * 2. Remove the padding in IE 10-.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n    box-sizing: border-box; /* 1 */\n    padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n    height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n    -webkit-appearance: textfield; /* 1 */\n    outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding and cancel buttons in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-cancel-button,\n[type=\"search\"]::-webkit-search-decoration {\n    -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n    -webkit-appearance: button; /* 1 */\n    font: inherit; /* 2 */\n}\n\n/* Interactive ========================================================================== */\n\n/*\n * Add the correct display in IE 9-.\n * 1. Add the correct display in Edge, IE, and Firefox.\n */\n\ndetails, /* 1 */\nmenu {\n    display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n    display: list-item;\n}\n\n/* Scripting ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\ncanvas {\n    display: inline-block;\n}\n\n/**\n * Add the correct display in IE.\n */\n\ntemplate {\n    display: none;\n}\n\n/* Hidden ========================================================================== */\n\n/**\n * Add the correct display in IE 10-.\n */\n\n[hidden] {\n    display: none;\n}\n","*,\n*::before,\n*::after {\n    box-sizing: border-box; // 1\n}\n\nbody {\n    overflow-x: hidden;\n    font-family: $font-family-base;\n    font-size: $font-size-sm;\n    line-height: $line-height-base;\n    background-color: $gray-900; // best practice, apply a default `background-color`\n    // background-image: linear-gradient(160deg, $gray-900 0%, $gray-800 100%);\n    background-attachment: fixed;\n    background-repeat: no-repeat;\n    color: $gray-100;\n    font-weight: $font-weight-light;\n    text-align: left;               // explicit initial text-align value so that we can later use `inherit`\n}\n\na {\n    color: inherit;\n}\na, a:link, a:hover, a:active, a:visited {\n    text-decoration: none;\n}\n\nfigure {\n    margin: 0;\n}\n\nfieldset {\n    border: none;\n}\n\ntextarea:focus, input:focus{\n    outline: none;\n    box-shadow: inset 0 0 6px rgba(0, 0, 0, .2);\n}\n\nlabel {\n    font-weight: bold;\n}\n\nh1, h2, h3 {\n    margin-top: 0;\n    line-height: 1.125em;\n    font-weight: $font-weight-normal;\n}\n\nh1 {\n    margin-bottom: $gutter;\n}\n\np { margin: 0; }\n\ndiv, section {\n    &:focus {\n        outline: none;\n    }\n}\n\n// helper classes\n.unselectable {\n    user-select: none;\n}\n\n.hide-text {\n    @include hide-text();\n}\n\n.center-icon {\n    position: relative;\n    &:before {\n        position: absolute;\n        width: 100% !important;\n        text-align: center !important;\n        top: 50%;\n        left: 0;\n        margin-top: -.5em;\n    }\n}\n\n.icon-only-icon {\n    &:before {\n        margin-right: 0 !important;\n    }\n}\n\n// typography\n.has-text-size-base     { font-size: $font-size-base; }\n.has-text-size-large    { font-size: $font-size-lg; }\n.has-text-size-larger   { font-size: $font-size-llg; }\n.has-text-size-largest  { font-size: $font-size-lllg; }\n.has-text-size-small    { font-size: $font-size-sm; }\n.has-text-size-smaller  { font-size: $font-size-ssm; }\n.has-text-size-smallest { font-size: $font-size-sssm; }\n\n\n// colors\n.has-background-transparent { background-color: transparent !important; }\n.has-background-white       { background-color: $white !important; }\n.has-background-black       { background-color: $black !important; }\n.has-background-gray-100    { background-color: $gray-100 !important; }\n.has-background-gray-200    { background-color: $gray-200 !important; }\n.has-background-gray-300    { background-color: $gray-300 !important; }\n.has-background-gray-400    { background-color: $gray-400 !important; }\n.has-background-gray-500    { background-color: $gray-500 !important; }\n.has-background-gray-550    { background-color: $gray-550 !important; }\n.has-background-gray-600    { background-color: $gray-600 !important; }\n.has-background-gray-700    { background-color: $gray-700 !important; }\n.has-background-gray-800    { background-color: $gray-800 !important; }\n.has-background-gray-900    { background-color: $gray-900 !important; }\n.has-background-danger      { background-color: $danger !important; }\n.has-background-info      { background-color: $info !important; }\n\n.has-text-transparent { color: transparent !important; }\n.has-text-white       { color: $white !important; }\n.has-text-black       { color: $black !important; }\n.has-text-gray-100    { color: $gray-100 !important; }\n.has-text-gray-200    { color: $gray-200 !important; }\n.has-text-gray-300    { color: $gray-300 !important; }\n.has-text-gray-400    { color: $gray-400 !important; }\n.has-text-gray-500    { color: $gray-500 !important; }\n.has-text-gray-550    { color: $gray-550 !important; }\n.has-text-gray-600    { color: $gray-600 !important; }\n.has-text-gray-700    { color: $gray-700 !important; }\n.has-text-gray-800    { color: $gray-800 !important; }\n.has-text-gray-900    { color: $gray-900 !important; }\n.has-text-danger      { color: $danger !important; }\n.has-text-info        { color: $info !important; }\n\n// orders\n.order-top    { order: 0 !important; }\n.order-1      { order: 1 !important; }\n.order-2      { order: 2 !important; }\n.order-3      { order: 3 !important; }\n.order-bottom { order: 100 !important; }\n\n","@mixin hide-text {\n    border: 0;\n    // overflow: hidden;\n    font: 0/0 a;\n    white-space: nowrap;\n    text-indent: 100%;\n    color: transparent;\n    text-shadow: none;\n}\n\n$text-inputs:   'input[type=\"color\"]',\n                'input[type=\"date\"]',\n                'input[type=\"datetime\"]',\n                'input[type=\"datetime-local\"]',\n                'input[type=\"email\"]',\n                'input[type=\"month\"]',\n                'input[type=\"number\"]',\n                'input[type=\"password\"]',\n                'input[type=\"search\"]',\n                'input[type=\"tel\"]',\n                'input[type=\"text\"]',\n                'input[type=\"time\"]',\n                'input[type=\"url\"]',\n                'input[type=\"week\"]',\n                'input:not([type])';\n// use like\n// #{$text-inputs} {\n//     background-color: red;\n// }\n\n$button-inputs: 'input[type=\"submit\"]',\n                'input[type=\"button\"]',\n                'input[type=\"reset\"]';\n$button-inputs-hover: 'input[type=\"submit\"]:hover',\n                      'input[type=\"button\"]:hover',\n                      'input[type=\"reset\"]:hover';\n\n// media breakpoint\n// $max-1024w:      \"(max-width: 1024px)\";\n// $max-768w:       \"(max-width: 770px)\";\n// $tablet:         \"(min-width: 768px) and (max-width: 1023px)\";\n// $desktop:        \"(min-width: 1024px)\";\n// @media screen and (max-width: 780px) {\n// }\n","main.layout {\n    display: grid;\n    grid-template-areas: \"main-sidebar  secondary-sidebar  content-header\"\n                         \"main-sidebar  secondary-sidebar  content\"\n                         \"main-sidebar  footer  footer\";\n    grid-template-columns: auto auto 1fr;\n    grid-template-rows: auto 1fr auto;\n    padding: $gutter;\n    min-height: 100vh;\n\n    > .layout-sidebar-primary {\n        grid-area: main-sidebar;\n\n        margin-right: 2px;\n    }\n\n    > .layout-sidebar-secondary {\n        grid-area: secondary-sidebar;\n    }\n\n    > .layout-header {\n        grid-area: content-header;\n\n        display: flex;\n        flex-direction: column;\n        justify-content: space-between;\n        height: 110px;\n    }\n\n    > .layout-content {\n        grid-area: content;\n    }\n\n    > .layout-footer {\n        grid-area: footer;\n\n        margin-top: $gutter * 4;\n        border-top: 1px solid #495057;\n        padding: $gutter * .5 0 0;\n        margin-left: 90px + $gutter;\n    }\n}\n","//  TUTTO DA FARE MA PRIMA L'HTML\n\n#{$text-inputs} {\n    padding: 0 .5em;\n    min-width: 5rem;\n    height: $gutter * 2;\n    border: none;\n    border-radius: 2px;\n    background-color: $gray-100;\n    font-family: $font-family-sans-serif;\n    line-height: 1.5em;\n\n    &:focus {\n        outline: none;\n        box-shadow: inset 0 0 2px $black;\n    }\n}\n\n#{$button-inputs},\nbutton, a.button {\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: top;\n\n    position: relative;\n    height: $gutter * 2;\n    box-shadow: none;\n    padding-bottom: calc(.375em - 1px);\n    padding-left: .75em;\n    padding-right: .75em;\n    padding-top: calc(.375em - 1px);\n\n    border-radius: 2px;\n    border: 1px solid transparent;\n\n    background-color: $gray-100;\n    color: $black;\n    font-family: $font-family-sans-serif;\n    font-size: $font-size-sm;\n    font-weight: $font-weight-light;\n    line-height: 1.5;\n\n    cursor: pointer;\n\n    user-select: none;\n    -moz-appearance: none;\n    -webkit-appearance: none;\n\n    &.bordered {\n        border: 1px solid $gray-600;\n    }\n\n    &[class^=\"icon-\"] {\n        min-width: auto;\n        height: auto;\n        padding: 0;\n    }\n\n    &:focus {\n        outline: none;\n        background-color: $gray-300;\n    }\n\n    &.is-dark {\n        background-color: $gray-700;\n        color: $gray-300;\n    }\n}\n\n#{$button-inputs-hover},\nbutton:hover,\na.button:hover {\n    background-color: $gray-600;\n    color: #FFF;\n    outline: 0;\n    cursor: pointer;\n}\n\ntextarea {\n    padding: .25em .5em;\n    min-width: 5rem;\n    border-radius: 2px;\n    border: none;\n    font-family: $font-family-sans-serif;\n}\n\nselect {\n    height: $gutter * 2;\n}\n\nlabel {\n    font-weight: normal;\n    > input {\n        margin-right: $gutter * .5;\n    }\n}\n\n\nform .fieldset,\nform fieldset {\n    padding: 0;\n    textarea, input {\n        &.error {\n            background-color: red;\n\n            &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */\n                color: white;\n                opacity: 1; /* Firefox */\n            }\n        }\n    }\n}\n\n\n// json editor\n.jsoneditor-container {\n    background-color: $gray-550;\n    .jsoneditor {\n        border: 0;\n\n        .jsoneditor-menu {\n            background: $gray-700;\n            border: 0;\n        }\n\n        .jsoneditor-contextmenu {\n            .jsoneditor-menu {\n                background: $gray-300;\n            }\n        }\n\n        button {\n            min-width: unset;\n        }\n    }\n}\n\n\n// Date Picker\n.clear-button {\n    transition: all 0.4s ease-in-out;\n    display: inline-block;\n    z-index: 10;\n    color: $gray-900;\n    font-size: 1.3em;\n    line-height: 25px;\n    margin-left: -30px;\n    padding: 0px 9px;\n    cursor: pointer;\n\n\n    &:hover {\n        background-color: $gray-800;\n        border-radius: 100%;\n        color: $white;\n    }\n}\n","// simple tag\n.tag:not(body) {\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n    height: 2em;\n    margin-right: $gutter * .25;\n    margin-bottom: $gutter * .25;\n    padding-left: .75em;\n    padding-right: .75em;\n    background-color: $gray-600;\n    border-radius: 4px;\n    color: $white;\n    font-size: $font-size-sssm;\n    line-height: 1.5;\n    white-space: nowrap;\n    user-select: none;\n}\n.tag-group {\n    align-items: center;\n    display: inline-flex;\n    justify-content: flex-start;\n    .tag {\n        margin-right: 0;\n        &:not(:first-child) {\n            border-bottom-left-radius: 0;\n            border-top-left-radius: 0;\n            margin-right: $gutter * .25;\n        }\n        &:not(:last-child) {\n            border-bottom-right-radius: 0;\n            border-top-right-radius: 0;\n        }\n    }\n}\n\n\n// simple box\n.box {\n    display: block;\n    padding: $gutter * .5;\n    border-radius: 4px;\n    background-color: $gray-200;\n    color: $gray-900;\n    box-shadow: 0 2px 3px rgba(10,10,10,.1), 0 0 0 1px rgba(10,10,10,.1);\n    overflow: hidden;\n}\n\n// flex columns\n.columns {\n    display: flex;\n    flex-wrap: wrap;\n    margin-left: -($gutter * .5);\n    margin-right: -($gutter * .5);\n    margin-top: -($gutter * .5);\n}\n\n.column {\n    display: block;\n    flex-basis: 0;\n    flex-grow: 1;\n    flex-shrink: 1;\n    padding: ($gutter * .5);\n    &.is-1 {\n        flex: none;\n        width: 100%;\n    }\n    &.is-2 {\n        flex: none;\n        width: 50%;\n    }\n    &.is-3 {\n        flex: none;\n        width: 33.33333%;\n    }\n    &.is-4 {\n        flex: none;\n        width: 25%;\n    }\n\n    &.is-one-third-1024 {\n        @media screen and (min-width: 769px) {\n            flex: none;\n            width: 33.33333%;\n        }\n    }\n    &.is-one-quarter-1024 {\n        @media screen and (min-width: 769px) {\n            flex: none;\n            width: 25%;\n        }\n    }\n\n    &.is-one-third-desktop {\n        @media screen and (min-width: 1088px) {\n            flex: none;\n            width: 33.33333%;\n        }\n    }\n    &.is-one-quarter-desktop {\n        @media screen and (min-width: 1088px) {\n            flex: none;\n            width: 25%;\n        }\n    }\n    &.is-one-fifth-desktop {\n        @media screen and (min-width: 1088px) {\n            flex: none;\n            width: 20%;\n        }\n    }\n\n    &.is-one-eight-bigscreen {\n        @media screen and (min-width: 1580px) {\n            flex: none;\n            width: 12.5%;\n        }\n    }\n\n\n    &.is-one-third-768 {\n        @media screen and (max-width: 768px) {\n            flex: none;\n            width: 33.33333%;\n        }\n    }\n    &.is-one-quarter-768 {\n        @media screen and (max-width: 768px) {\n            flex: none;\n            width: 25%;\n        }\n    }\n}\n\n\n// loader\n.is-loading-spinner:after {\n    animation: spinAround .5s infinite linear;\n    border: 2px solid #dbdbdb;\n    border-radius: 290486px;\n    border-right-color: transparent;\n    border-top-color: transparent;\n    content: \"\";\n    display: block;\n    height: 16px;\n    position: relative;\n    width: 16px;\n}\n\n@keyframes spinAround {\n    from {\n        transform: rotate(0)\n    }\n\n    to {\n        transform: rotate(359deg)\n    }\n}\n",".tree-view {\n    .list-actions {\n        margin: 10px;\n        text-align: center;\n    }\n\n    label {\n        color: $gray-600;\n    }\n    .tree-related-object label {\n        color: $gray-100;\n    }\n\n\n    .tree-list-single-choice {\n        .node-element {\n            border: 1px solid transparent;\n\n            &.disabled {\n                opacity: 0.7;\n            }\n\n            &:not(.disabled):hover {\n                border: 1px solid #ff5460;\n                cursor: pointer;\n            }\n\n            &.tree-related-object{\n                background-color: #ff5460;\n                border: 1px solid #ff5460;\n\n                &.was-related-object{\n                    background-color: transparent;\n                    border: 1px dashed #ff5460;\n                }\n            }\n        }\n    }\n\n    .tree-entry-list {\n\n        .tree-list-node {\n            // margin-left: 23px;\n\n            .node-children {\n                margin-left: 23px;\n            }\n\n            &.root-node {\n                margin-left: 0;\n            }\n\n            .icon-blank {\n                width: 23px;\n                visibility: hidden;\n            }\n\n            .icon {\n                cursor: pointer;\n            }\n\n            .toggle-tree-children {\n                display: inline-block;\n                transition: all 0.3s linear;\n                font-size: 20px;\n                width: 12px;\n                height: 12px;\n                line-height: 12px;\n                padding: 0;\n                cursor: pointer;\n\n                &.open {\n                    transform: rotate(90deg);\n                }\n            }\n\n            input {\n                cursor: pointer;\n                display: inline-block;\n            }\n\n            label {\n                display: inline-block;\n\n                &.is-folder {\n                    cursor: pointer;\n                }\n            }\n        }\n    }\n}\n","// RelatedObjects\n\n.relations {\n    .emptyResults {\n        text-align: center;\n        padding: 0.45em 0;\n    }\n}\n\n.relation-view {\n    .related-object, .add-related-object {\n        // max-height: 60px;\n        opacity: 1;\n        transition: opacity .5s ease-in, max-height 0.4s ease-in;\n        will-change: opacity, max-height;\n\n        &.staggered-enter-active {\n            opacity: 0;\n            // max-height: 0;\n        }\n\n        &.staggered-leave-to {\n            transition: none;\n            will-change: none;\n        }\n    }\n\n    .related-list{\n        .added-related-object {\n            // opacity: 0.7;\n            color: #75b08a;\n\n            &:last-child {\n                border-bottom: 1px solid;\n            }\n        }\n        .list-header {\n            border-bottom: 1px solid;\n        }\n\n        .related-object {\n            &.deleted {\n                span {\n                    text-decoration: line-through;\n                    color: #ff5460;\n                }\n\n                & + .related-object:not(.deleted) {\n                    border-top: 1px solid;\n                    border-color: #ff5460;\n                }\n\n                & + .related-object.deleted {\n                    border-top: 0;\n                    border-color: #ff5460;\n                }\n            }\n\n            & + .related-object.deleted {\n                border-top: 1px solid;\n                border-color: #ff5460;\n            }\n        }\n\n        .related-object, .list-header, .added-related-object {\n            display: grid;\n            grid-template-columns: auto 40px 175px 50px 80px 35px;\n\n            span {\n                padding: 5px 5px;\n                // border: 1px solid white;\n\n                &.title {\n                    padding-left: 10px;\n                    flex-grow: 2;\n                }\n                &.uname {\n                    flex-grow: 1;\n                }\n            }\n\n            a {\n                &.open {\n                    height: 25px;\n                    margin: 5px 5px;\n                    color: black;\n                    background-color: white;\n                    border-radius: 2px;\n                    line-height: 24px;\n                    text-align: center;\n\n                    &:hover {\n                        background-color: $gray-700;\n                        color: $white;\n                        outline: 0;\n                        cursor: pointer;\n                    }\n                }\n            }\n\n            button {\n                &.remove, &.add {\n                    padding: 0;\n                    height: 25px;\n                    margin: 5px 5px;\n                    min-width: unset;\n                }\n            }\n        }\n\n        .header {\n            background-color: $gray-700;\n        }\n    }\n\n    .relationships-view {\n        .filter-bar {\n            padding: 0 10px;\n\n            input[type=text] {\n                width: 100%;\n            }\n        }\n\n        .add-related-object, .add-related-list-header {\n            display: grid;\n            grid-template-columns: 20px auto 75px 105px 50px;\n\n            span {\n                padding: 5px 5px;\n\n                &.title {\n                    padding-left: 10px;\n                }\n            }\n\n            a {\n                &.open {\n                    height: 25px;\n                    margin: 5px 5px;\n                    color: black;\n                    background-color: $white;\n                    border-radius: 2px;\n                    line-height: 24px;\n                    text-align: center;\n\n                    &:hover {\n                        background-color: $gray-700;\n                        color: $white;\n                        outline: 0;\n                        cursor: pointer;\n                    }\n                }\n            }\n\n            button {\n                &.remove {\n                    padding: 0;\n                    height: 25px;\n                    margin: 5px 5px;\n                    min-width: unset;\n                }\n            }\n        }\n\n        .fade-enter-active, .fade-leave-active {\n            transition: opacity .5s;\n        }\n        .fade-enter, .fade-leave-to {\n            opacity: 0;\n        }\n    }\n\n    .related-object {\n        display: inline-block;\n    }\n\n    .animated-list-enter-active {\n        transition: all 0.5s;\n    }\n    .animated-list-enter/* .list-leave-active below version 2.1.8 */ {\n        opacity: 0;\n        transform: translateY(30px);\n    }\n\n\n    .modal-view {\n        position: fixed;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        z-index: 100;\n        background: rgba(0,0,0, 0.5);\n        overflow-y: hidden;\n\n        .modal-box {\n            display: flex;\n            flex-direction: column;\n            background-color: black;\n            margin: 13% auto;\n            width: 60%;\n            height: 70%;\n            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5),0 6px 20px 0 rgba(0,0,0,0.5);\n\n            .modal-header {\n                text-align: right;\n\n                button.close {\n                    background: transparent;\n                    color: $white;\n                    min-width: auto;\n                    font-size: 25px;\n                    margin: 0;\n                }\n            }\n\n            .modal-content {\n                padding: 10px;\n                overflow-y: auto;\n\n                &::-webkit-scrollbar {\n                    width: 15px;\n                }\n\n                &::-webkit-scrollbar-track {\n                    background: $black;\n                }\n                &::-webkit-scrollbar-thumb {\n                    background: #dddddd;\n                }\n\n                &::-webkit-scrollbar-thumb:hover {\n                    background: $white;\n                }\n            }\n        }\n    }\n}\n\n\n","#messagesDiv {\n    position: absolute;\n    top: 0; left: 0;\n    width: 100%; height: 100%;\n    background-color: rgba(0,0, 0, .65);\n    font-size: 12px;\n    z-index: 1000;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    &:empty {\n        display: none;\n    }\n\n    .message {\n        display: flex;\n        flex-direction: column;\n        align-items: flex-start;\n        margin-bottom: $gutter;\n        padding: $gutter / 2;\n        width: $gutter * 25;\n        min-height: $gutter * 11;\n        background-color: $gray-100;\n        color: $gray-900;\n        border-width: .5rem;\n        border-style: solid;\n        border-color: transparent;\n        border-radius: 1rem;\n        opacity: 0;\n        visibility: hidden;\n        clip-path: polygon(0 0, 100% 0, 20% 100%, 0 100%);\n        transition: .3s all;\n\n        &.on {\n            opacity: 1;\n            visibility: visible;\n            transform: translateY(0);\n            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);\n        }\n\n        &.details {\n            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);\n            width: 580px;\n            pre {\n                display: initial;\n            }\n        }\n\n        h1, h2, p {\n            margin: 0;\n        }\n\n        h2 {\n            font-size: 1.25em;\n        }\n\n        pre {\n            display: none;\n            width: 100%;\n            overflow: auto;\n        }\n\n        a.btn-close {\n            margin: auto 0 0;\n            border-top: 1px solid $gray-500;\n            padding: 2px 0 0 0;\n        }\n\n        &.success {\n            border-color: $gray-100;\n        }\n        &.info {\n            border-color: rgb(38, 84, 170);\n        }\n        &.error {\n            border-color: rgb(187, 26, 26);\n        }\n        &.warning {\n            border-color: rgb(202, 126, 26);\n        }\n    }\n}\n",".toolbar-filters {\n    input.search {\n        ::placeholder {\n            color: #DDD;\n        }\n    }\n}\n",".toolbar-pagination {\n    .results {\n        display: inline-block;\n        margin-right: 1em;\n    }\n    .pagination {\n        display: inline-block;\n        ul {\n            display: flex;\n            margin: 0;\n            padding: 0;\n            list-style: none;\n\n            li {\n                margin-right: .5em;\n                &:last-child {\n                    margin: 0;\n                }\n            }\n        }\n    }\n    .page-size {\n        display: inline-block;\n        > select {\n            margin-left: .5em;\n        }\n    }\n}\n",".menu-colophon {\n    font-size: 11px;\n    color: $gray-500;\n    font-weight: 100;\n}\n",".commands-menu {\n    position: sticky;\n    top: $gutter;\n    width: 90px;\n    margin-right: $gutter;\n    display: flex;\n    flex-direction: column;\n\n    button, input[type=submit] {\n        width: 100%;\n    }\n\n    a {\n        display: block;\n    }\n\n    > .project {\n        display: block;\n        width: 100%;\n        height: 86px;\n        margin-bottom: $gutter * 1.5;\n        background-image: url(\"data:image/svg+xml;base64,\");\n        background-color: $white;\n        background-size: 50%;\n        background-repeat: no-repeat;\n        background-position: center;\n        a {\n            width: 100%;\n            height: 100%;\n        }\n    }\n\n    > .commands-module {\n        width: 100%;\n        height: 86px;\n        margin-bottom: 2px;\n\n        > a {\n            display: block;\n            background-color: $gray-600;\n            color: white;\n            padding: 0 6px;\n            margin-bottom: 2px;\n            line-height: 20px;\n        }\n\n        &.squared {\n            a {\n                display: flex;\n                justify-content: center;\n                align-items: center;\n                width: 100%;\n                height: 100%;\n                padding: 1px 6px;\n            }\n        }\n    }\n\n    > .commands-buttons {\n        a {\n            display: block;\n            height: 2rem;\n            margin-bottom: 2px;\n            border-radius: 2px;\n            background-color: white;\n            color: #222;\n            text-align: center;\n            line-height: 2rem;\n        }\n\n        button, input[type=submit] {\n            margin-bottom: 2px;\n        }\n\n    }\n\n    > .commands-footer {\n        flex-grow: 1;\n    }\n\n    // .trashcan {\n    //     display: flex;\n    //     justify-content: center;\n    //     align-items: center;\n    //     height: 80px;\n    //     margin-top: $gutter;\n    //     background-color: transparent;\n    //     color: $gray-200;\n    //     font-size: 2rem;\n    //     &:before {\n    //         margin: 0;\n    //     }\n    // }\n}\n",".layout-header {\n    ul[role=\"menu\"] {\n        display: flex;\n        list-style: none;\n        margin: 0 0 $gutter * 3 $gutter * .5;\n        padding: 0;\n\n        > li {\n            position: relative;\n            width: 24px;\n            height: 24px;\n            margin-right: 1px;\n            background-color: $gray-600;\n            @media screen and (min-width: 780px) {\n                width: 38px;\n                height: 38px;\n            }\n\n            &.separator {\n                flex-grow: 1;\n            }\n\n            &.current a:after {\n                content: '\\00a0 ';\n                display: block;\n                position: absolute;\n                width: 100%; height: 6px;\n                bottom: 0;\n                left: 0;\n                background-color: $white;\n                opacity: .3;\n                @media screen and (max-width: 780px) {\n                    width: 6px; height: 6px;\n                    top: 50%; margin-top: -3px;\n                    left: 50%; margin-left: -3px;\n                }\n            }\n\n            a {\n                position: relative;\n                display: block;\n                width: 100%;\n                height: 100%;\n                line-height: 1;\n                padding: 4px 6px;\n\n                @media screen and (max-width: 780px) {\n                    @include hide-text;\n                }\n\n                &:hover:before {\n                    position: absolute;\n                    content: attr(tooltip);\n                    top: 24px;\n                    left: 0;\n                    z-index: 1;\n                    text-indent: initial;\n\n                    white-space: nowrap;\n                    font: .75rem $font-family-sans-serif;\n                    color: $gray-200;\n\n                    @media screen and (min-width: 780px) {\n                        top: 42px;\n                    }\n                }\n            }\n        }\n    }\n}\n",".element-menu {\n    > * {\n        margin: 0 0 $gutter 0;\n    }\n    > *:empty {\n        display: none;\n    }\n\n    > .project {\n        display: block;\n        width: 282px;\n        height: 282px;\n        overflow: hidden;\n        padding: 140px 1rem 1rem;\n        margin-bottom: $gutter * 1.5;\n        border-bottom: none;\n        color: #bd8411;\n\n        &.dark {\n            background-image: url(\"data:image/svg+xml;base64,\");\n            background-color: $gray-900;\n        }\n\n        background-image: url(\"data:image/svg+xml;base64,\");\n        background-color: $white;\n        background-size: 50%;\n        background-repeat: no-repeat;\n        background-position: 1rem 1rem;\n        font: normal 1.5em $font-family-serif;\n        word-break: break-word;\n\n        // andranno delle variabili per i breakpoint delle mediaquery\n        @media screen and (max-width: 960px) {\n            width: 140px;\n            height: 140px;\n            padding: 75px .5rem .5rem;\n            background-position: .5rem .55rem;\n            margin-right: $gutter;\n        }\n\n        body.view-module & {\n            width: 158px;\n            height: 148px;\n            padding: 88px 1rem 1rem;\n            background-position: 1rem 1rem;\n            border-right: 1rem solid $gray-600;\n        }\n    }\n\n    > .menu-links {\n        list-style: none;\n        padding: 0;\n        li {\n            margin: $gutter 0;\n        }\n    }\n\n    > .menu-actions {\n        list-style: none;\n        padding: 0;\n\n        a {\n            display: block;\n\n            &:hover {\n                color: white;\n            }\n        }\n\n        li {\n            line-height: 1.5em;\n            padding: .5em 0;\n            border-bottom: 1px solid $gray-700;\n\n            &:first-child {\n                padding-top: 0;\n            }\n\n            &:last-child {\n                border-bottom: none;\n            }\n        }\n    }\n}\n\n\n","// override default layout\n\nbody.view-login {\n\n    height: 100vh;\n    background-color: $white;\n    background-image: linear-gradient(to bottom right, $gray-500 0%, $white 100%);\n    background-attachment: fixed;\n    background-repeat: no-repeat;\n\n    main {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        height: 100%;\n        .messagesDiv {\n            display: block;\n        }\n        .left-menu {\n            display: none;\n        }\n\n        .login-form {\n            position: relative;\n            display: grid;\n            grid-template: \"login-logo login-form\" \"login-footer login-footer\";\n\n            &:before {\n                content: '\\00a0 ';\n                display: block;\n                background-image: url(\"data:image/svg+xml;base64,\");\n                background-color: $gray-900;\n                background-repeat: no-repeat;\n                background-position: center;\n                background-size: 66%;\n                width: 180px;\n                height: 180px;\n                margin-right: $gutter;\n            }\n\n            form {\n                display: flex;\n                flex-direction: column;\n                justify-content: space-between;\n\n                > div {\n                    width: 240px;\n                }\n\n                label {\n                    font-size: 11px;\n                    color: $gray-550;\n                    font-weight: normal;\n                    padding-left: .25em;\n                    margin: 1px 0 $gutter;\n                }\n\n                label, input {\n                    display: block;\n                    width: 100%;\n                }\n            }\n\n            footer {\n                grid-area: login-footer;\n                // display: flex;\n                flex-grow: 0;\n                justify-content: space-between;\n                margin-top: $gutter;\n                padding: 0 2px 0;\n                font-size: .875em;\n                font-weight: 300;\n                line-height: 1.4em;\n                color: $gray-600;\n                white-space: nowrap;\n                letter-spacing: .25px;\n\n                span {\n                    margin: 0 .25em;\n                }\n            }\n\n            @media screen and (max-width: 780px) {\n                display: block;\n\n                &:before {\n                    width: 100%;\n                    padding-bottom: 100%;\n                    margin: 0 0 $gutter * 2;\n                }\n\n                footer {\n                    display: block;\n                    span {\n                        display: block;\n                        height: 0;\n                        visibility: hidden;\n                        overflow: hidden;\n                    }\n                }\n            }\n\n            #messagesDiv {\n                display: block;\n                top: auto;\n                width: 180px;\n                height: auto;\n                bottom: 100%;\n                background: none;\n                margin-bottom: 1px;\n                @media screen and (max-width: 780px) {\n                    width: 100%;\n                }\n\n                .message {\n                    margin: 0;\n                    width: 100%;\n                    min-height: auto;\n                    border: none;\n                    border-radius: 0;\n                    background-color: #900;\n                    color: #fff;\n                    clip-path: none;\n                    h2 { display: none; }\n                    a { display: none; }\n                }\n            }\n        }\n    }\n}\n","/*****************/\n/*** DASHBOARD ***/\n/*****************/\n\nbody.view-dashboard {\n    width: 100vw;\n\n    .dashboard {\n        flex: auto;\n\n        header {\n            h2 {\n                margin-bottom: $gutter * .75;\n            }\n        }\n\n        .dashboard-items {\n            display: flex;\n            align-items: stretch;\n            flex-wrap: wrap;\n            padding: 0;\n            margin: 0 0 $gutter * 1.5;\n            min-width: calc(100% + #{$gutter / 2});\n            list-style: none;\n\n            > li {\n                position: relative;\n                display: block;\n                flex-basis: $gutter * 8.75;\n                width: $gutter * 8.75;\n                height: $gutter * 8.75;\n                background-color: $gray-700;\n                margin-bottom: $gutter * .125;\n                margin-right: $gutter * .125;\n\n                // used for icon inside\n                &:before {\n                    position: absolute;\n                    top: 0;\n                    right: .25rem;\n                    color: $white;\n                    font-size: 12px;\n                    text-align: right !important;\n                    line-height: 1.5rem !important;\n                    opacity: 1 !important;\n                    transition: .3s all;\n                    z-index: 11;\n                }\n                &.has-text-danger:before {\n                    color: $danger !important;\n                }\n\n                &:after {\n                    content: '\\00a0 ';\n                    position: absolute;\n                    display: block;\n                    width: 100%;\n                    height: 1.5rem;\n                    background-color: $black;\n                    opacity: .2;\n                    transition: .3s all;\n                    z-index: 10;\n                }\n\n                a {\n                    position: absolute;\n                    display: block;\n                    z-index: 20;\n                    width: 100%;\n                    height: 100%;\n                    padding: 0 8px;\n                    font-size: $font-size-ssm;\n                    line-height: 1.5rem;\n                    color: white;\n                    transition: .3s all;\n                }\n\n                &:hover {\n                    a {\n                        line-height: 2rem;\n                        padding-left: 12px;\n                    }\n\n                    &:after {\n                        height: 2rem;\n                        opacity: .5;\n                    }\n\n                    &:before {\n                        line-height: 2rem !important;\n                    }\n                }\n            }\n        }\n\n        .dashboard-items.dashboard-utility {\n            // used for icon inside\n            >li:before {\n                top: 50%;\n                left: 0;\n                margin-top: -1rem;\n                width: 100%;\n                color: $white;\n                font-size: 2rem;\n                text-align: center !important;\n                line-height: 1 !important;\n                opacity: .2 !important;\n                transition: none;\n            }\n            >li.has-text-danger:before {\n                opacity: .4 !important;\n            }\n\n\n            >li:after {\n                content: '';\n                display: none;\n            }\n\n            >li:hover {\n                a {\n                    line-height: 1.5rem;\n                    padding-left: 8px;\n                }\n\n                &:before {\n                    line-height: 1 !important;\n                    opacity: .8 !important;\n                }\n            }\n\n        }\n    }\n}\n","body.view-module {\n\n    // index\n    .module-header {\n        height: 86px;\n        margin-left: $gutter * .5;\n\n        .module-toolbar {\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n            @media screen and (max-width: 960px) {\n                height: 100%;\n                flex-direction: column;\n                justify-content: flex-start;\n                align-items: flex-start;\n                >* {\n                    margin-bottom: $gutter;\n                }\n            }\n        }\n    }\n\n    .module-index {\n\n        .list-objects {\n            display: table;\n            border-collapse: collapse;\n            min-width: 100%;\n\n            > nav {\n                display: table-row;\n\n                &.table-header {\n                    border-bottom: 1px solid $gray-700;\n\n                    a.sort {\n                        display: flex;\n                        align-items: center;\n\n                        &:after {\n                            content: \"›\";\n                            margin-left: 10px;\n                            display: inline-block;\n                            font-size: 1.5em;\n                            line-height: 21px;\n                        }\n\n                        &.up:after {\n                            transform: rotate(-90deg);\n                        }\n                        &.down:after {\n                            transform: rotate(90deg);\n                        }\n                    }\n                }\n            }\n\n            > a {\n                display: table-row;\n\n                &:hover {\n                    background-color: $gray-700;\n                }\n            }\n\n            > nav, > a {\n                div {\n                    display: table-cell;\n                    padding: $gutter * .5;\n                    white-space: nowrap;\n\n                    &.title-cell {\n                        max-width: 310px;\n                        text-overflow: ellipsis;\n                        overflow: hidden;\n                        &:empty:before {\n                            content: attr(untitled);\n                            color: $gray-600;\n                            font-style: italic;\n                        }\n                    }\n\n                    &.thumb-cell {\n                        max-width: $gutter * 2.5;\n                        height: $gutter * 2;\n                        padding: 0;\n                        overflow: hidden;\n                        img {\n                            padding: 1px;\n                            display: inline-block;\n                            max-height: 100%;\n                            max-width: 100%;\n                            vertical-align: middle;\n                            background-color: $gray-200;\n                        }\n                        .icon {\n                            display: inline-block;\n                            width: 100%;\n                            font-size: 18px;\n                            padding: 6px;\n                        }\n                    }\n\n                    &:last-child {\n                        text-align: right;\n                        > form {\n                            display: inline-block;\n                        }\n                    }\n                }\n            }\n\n            button {\n                line-height: initial;\n            }\n        }\n\n        .bulk-actions {\n            border-top: 1px solid $gray-700;\n\n            header {\n                width: 100%;\n            }\n\n            nav {\n                display: flex;\n                form {\n                    margin-right: $gutter;\n                }\n            }\n        }\n    }\n\n    // TODO: da fare meglio\n    &.module-media, &.module-images, &.module-video, &.module-audio, &.module-objects {\n       .module-index .list-objects > a div.title-cell {\n           max-width: 210px;\n       }\n    }\n\n    .section.relations {\n        width: 100%;\n        margin-top: $gutter;\n    }\n\n}\n","body.view-module {\n\n    .modules-view {\n        padding-left: $gutter * .5;\n        >header {\n            h1:empty {\n                &:before {\n                    content: attr(untitled);\n                    color: $gray-600;\n                    font-style: italic;\n                }\n            }\n        }\n    }\n\n    // view\n    .module-form {\n        width: 100%;\n        margin-top: $gutter;\n\n        header {\n            width: 100%;\n        }\n\n        form {\n            // layout\n            display: flex;\n            flex-wrap: wrap;\n\n            .form-column {\n                display: contents;\n                width: 100%;\n\n                @media screen and (min-width: 780px) {\n                    display: block;\n                    &:first-of-type { width: 67%; padding-right: $gutter; }\n                    &:last-of-type { width: 33%; }\n                }\n                @media screen and (min-width: 1024px) {\n                    &:first-of-type { width: 55%; padding-right: 2 * $gutter; }\n                    &:last-of-type { width: 45%; }\n                }\n            }\n\n            section.fieldset {\n                width: 100%;\n                order: 0;\n            }\n\n\n            // section content\n            section.fieldset {\n                > div,\n                .tab-container >div {\n                    margin: $gutter 0;\n                    &:first-child {\n                        margin-top: 0;\n                    }\n                }\n\n                .tab-container {\n                    margin-bottom: $gutter * 3;\n                }\n\n                .tab {\n                    display: flex;\n                    align-items: center;\n                    margin-bottom: $gutter;\n                    padding: .25rem .5rem;\n                    background-color: $gray-800;\n                    color: $gray-500;\n                    border-radius: 3px;\n                    cursor: pointer;\n\n                    &.is-loading-spinner {\n                   